<template>
  <div id="app">
    <s-header></s-header>
    <div class="tab">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/near">附近</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import {header} from "@/components";
import Fingerprint from 'fingerprintjs';
export default {
  name: "App",
  components: {
    "s-header": header
  },
  created() {
    console.log(new Fingerprint().get());
    this.$router.push({
      name: "goods"
    });
  }
};
</script>
<style lang="scss" scoped>
#app {
  .tab {
    display: flex;
    width: 100%;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #e9eaec;
    .tab-item {
      flex: 1;
      text-align: center;
      & > a {
        display: block;
        font-size: 14px;
        color: rgb(77, 85, 93);
        &.router-link-active {
          color: rgb(240, 20, 20);
        }
      }
    }
  }
}
</style>



