import Vue from 'vue';
import VueRouter from 'vue-router';
import { routers } from '@/router/router';

Vue.use(VueRouter);

// 路由配置
const RouterConfig = {
  mode: 'hash',
  base: __dirname,
  routes: routers
};
const router = new VueRouter(RouterConfig);

router.beforeEach((to, from, next) => {
  next();
});
router.afterEach(() => {
  window.scrollTo(0, 0);
});
export default router;


