export const goods = {
    path: '/goods',
    name: 'goods',
    meta: {
        title: '商品'
    },
    component: resolve => {
        require(['@/views/goods'], resolve);
    }
};
export const ratings = {
    path: '/ratings',
    name: 'ratings',
    meta: {
        title: '评价'
    },
    component: resolve => {
        require(['@/views/ratings'], resolve);
    }
};
export const near = {
    path: '/near',
    name: 'near',
    meta: {
        title: '附近'
    },

    component: resolve => {
        require(['@/views/near'], resolve);
    }
};
export const routers = [
    goods,
    ratings,
    near
];


